#realistic-marker-highlight{
  position: relative;
}

#realistic-marker-highlight:before{
  content: "";
  background: linear-gradient(148deg, #9039ff 7%, #f147f0 41%, #ff6e6b 93%);
  height: 100%;
  position: absolute;
  z-index: -1;
  -webkit-filter: url(#marker-shape);
  filter: url(#marker-shape);
  left: -0.2em;
  top: 0.1em;
  animation: animateHighlight 2s forwards ease 0.7s;
}

@keyframes animateHighlight {
  from {
    width: 0;
  }

  to {
    width: 110%;
  }
}

.mouse {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  width: 1.3em;
  height: 2.3em;
  border: 2px solid white;
  border-radius: 60px;
}

.mouse::before {
  content: '';
  width: 4px;
  height: 4px;
  position: absolute;
  top: 3px;
  left: 50%;
  transform: translateX(-50%);
  background-color: white;
  border-radius: 50%;
  opacity: 1;
  animation: wheel 2s infinite;
  -webkit-animation: wheel 2s infinite;
}

@keyframes wheel {
  to {
    opacity: 0;
    top: 30px;
  }
}
.text-gradient {
  background: linear-gradient(148deg, #9039ff 7%, #f147f0 41%, #ff6e6b 93%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.image-container {
  position: relative;
  margin-bottom: 1em;
  overflow: hidden;
}

.image {
  transition: transform 250ms ease-in-out;
}

.image-container:hover .image {
  transform: rotate(2deg) scale(1.05);
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: background-color 250ms ease-in-out;
}

.overlay:hover {
  background-color: rgba(0, 0, 0, 0.25);
}

.info-label {
  position: absolute;
  left: 5%;
  bottom: 5%;
  display: grid;
  place-items: center;
  height: 3rem;
  width: 3rem;
  opacity: 0;
}

.overlay:hover .info-label {
  opacity: 100;
  transition: opacity 500ms ease;
}

.info {
  position: absolute;
  left: 5%;
  bottom: 5%;
  display: grid;
  place-items: center;
  overflow: hidden;
  padding: 2em;
  border-radius: 1.4em;
  background-color: rgba(0, 0, 0, 0.85);
  max-height: 90%;
  max-width: 90%;
  -webkit-clip-path: circle(0% at 0% 100%);
  clip-path: circle(0% at 0% 100%);
  transition: -webkit-clip-path 500ms ease-in-out;
  transition: clip-path 500ms ease-in-out;
  transition: clip-path 500ms ease-in-out, -webkit-clip-path 500ms ease-in-out;
}

.info-container {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.info-container:hover .info {
  -webkit-clip-path: circle(75%);
  clip-path: circle(75%);
}

.link-container {
  position: absolute;
  right: 5%;
  bottom: 5%;
  display: flex;
  flex-direction: row;
}

.link {
  width: 3em;
  height: 3em;
  padding: 0.5em;
  opacity: 0;
}

.overlay:hover .link {
  opacity: 100;
  transition: opacity 500ms ease;
}

.link:hover {
  background-color: rgba(255, 255, 255, 0.25);
}

